            学成教育隐私政策
            提示条款
            您的信任对我们非常重要，唯品会（“我们”）深知个人信息对您的重要性，我们将按照法律法规要求，采取相应安全保护措施，尽力保护您的个人信息。您在使用我们的产品与/或服务时，我们可能会收集和使用您的相关信息。我们希望通过《唯品会隐私政策》（“本隐私政策”）向您说明我们在您使用我们的产品与/或服务时如何收集、使用、保存、共享和转让这些信息，以及我们为您提供的访问、更新、删除和保护这些信息的方式。


            本政策将帮助您了解以下内容：


            1、我们如何收集和使用您的个人信息
            2、我们如何使用 Cookie 和同类技术
            3、我们如何共享、转让、公开披露您的个人信息
            4、我们如何保护和保存您的个人信息
            5、您如何管理个人信息
            6、我们如何处理未成年人的个人信息
            7、通知和修订
            8、如何联系我们


            本隐私政策与您所使用的唯品会商城服务以及该服务所包括的各种业务功能（以下统称“我们的产品与/或服务”）息息相关，希望您在使用我们的产品与/或服务前仔细阅读并确认您已经充分理解本政策所写明的内容，并让您可以按照本隐私政策的指引做出您认为适当的选择。本隐私政策中涉及的相关术语，我们尽量以简明扼要的表述，并提供进一步说明的链接，以便您更好地理解。您使用或在我们更新本隐私政策后（我们会及时提示您更新的情况）继续使用我们的产品与/或服务，即意味着您同意本隐私政策(含更新版本)内容，并且同意我们按照本隐私政策收集、使用、保存和共享您的相关信息。


            如对本隐私政策或相关事宜有任何问题，请联系唯品会客户服务部，客户服务电话：400-6789-888。


